import { VideoModalProps } from 'tradewize-component';
import { ConfirmModalProps } from '../../../components/Modal/ConfirmModal';
import {
  SHOW_ALERT_ASYNC,
  HIDE_ALERT_ASYNC,
  HIDE_ALERT,
  SHOW_ALERT,
  SHOW_LOADING_OVERLAY,
  HIDE_LOADING_OVERLAY,
  SHOW_LOADING_OVERLAY_ASYNC,
  HIDE_LOADING_OVERLAY_ASYNC,
  SET_UNREAD_MESSAGES,
  HIDE_SELECTED_LANGUAGE_MODAL,
  SHOW_SELECTED_LANGUAGE_MODAL,
  SHOW_CONFIRM_MODAL,
  HIDE_CONFIRM_MODAL,
  LANGUAGE,
  HIDE_VIDEO_MODAL,
  SHOW_VIDEO_MODAL,
  SET_MEDIA,
  HIDE_CUSTOM_MODAL,
  SHOW_CUSTOM_MODAL,
  SET_FIRST_SCREEN_OPEN_APP,
  HIDE_SELECT_BOTTOM_MODAL,
  SHOW_SELECT_BOTTOM_MODAL,
} from './constants';
import { AppAlertModalProps, AppLoadingOverlayProps } from './types';
import { CustomModalProps } from '../../../components/Modal';
import { SelectBottomModalProps } from '../../../components/Modal/SelectBottomModal';

// action saga
export const showAlertAsync = (payload: AppAlertModalProps) =>
  ({ type: SHOW_ALERT_ASYNC, payload } as const);
export const hideAlertAsync = () => ({ type: HIDE_ALERT_ASYNC } as const);
export const showLoadingOverlayAsync = (payload?: AppLoadingOverlayProps) =>
  ({ type: SHOW_LOADING_OVERLAY_ASYNC, payload } as const);
export const hideLoadingOverlayAsync = () =>
  ({ type: HIDE_LOADING_OVERLAY_ASYNC } as const);

// action reducer
export const showAlert = (payload: AppAlertModalProps) =>
  ({ type: SHOW_ALERT, payload } as const);
export const hideAlert = () => ({ type: HIDE_ALERT } as const);
export const showLoadingOverlay = (payload: AppLoadingOverlayProps) =>
  ({ type: SHOW_LOADING_OVERLAY, payload } as const);
export const hideLoadingOverlay = () =>
  ({ type: HIDE_LOADING_OVERLAY } as const);
export const setUnreadMessages = (payload: number) =>
  ({ type: SET_UNREAD_MESSAGES, payload } as const);
export const showSelectedLanguageModal = () =>
  ({ type: SHOW_SELECTED_LANGUAGE_MODAL } as const);
export const hideSelectedLanguageModal = () =>
  ({ type: HIDE_SELECTED_LANGUAGE_MODAL } as const);
export const showConfirmModal = (payload: ConfirmModalProps) =>
  ({ type: SHOW_CONFIRM_MODAL, payload } as const);
export const hideConfirmModal = () => ({ type: HIDE_CONFIRM_MODAL } as const);
export const setLanguage = (payload: string) =>
  ({ type: LANGUAGE, payload } as const);
export const showVideoModal = (payload: VideoModalProps) =>
  ({ type: SHOW_VIDEO_MODAL, payload } as const);
export const hideVideoModal = () => ({ type: HIDE_VIDEO_MODAL } as const);
export const setMedia = (payload: any) =>
  ({ type: SET_MEDIA, payload } as const);
export const showCustomModal = (payload: CustomModalProps) =>
  ({ type: SHOW_CUSTOM_MODAL, payload } as const);
export const hideCustomModal = () => ({ type: HIDE_CUSTOM_MODAL } as const);
export const setFirstScreenOpenApp = (payload: string | null) =>
  ({ type: SET_FIRST_SCREEN_OPEN_APP, payload } as const);

export const showSelectBottomModal = (payload: SelectBottomModalProps) =>
  ({ type: SHOW_SELECT_BOTTOM_MODAL, payload } as const);
export const hideSelectBottomModal = () =>
  ({ type: HIDE_SELECT_BOTTOM_MODAL } as const);

export const actionApp = {
  showAlertAsync,
  hideAlertAsync,
  showLoadingOverlayAsync,
  hideLoadingOverlayAsync,
  showAlert,
  hideAlert,
  showLoadingOverlay,
  hideLoadingOverlay,
  setUnreadMessages,
  showSelectedLanguageModal,
  hideSelectedLanguageModal,
  showConfirmModal,
  hideConfirmModal,
  setLanguage,
  showVideoModal,
  hideVideoModal,
  setMedia,
  showCustomModal,
  hideCustomModal,
  setFirstScreenOpenApp,
  showSelectBottomModal,
  hideSelectBottomModal,
};

export type AppAction =
  | ReturnType<typeof showAlert>
  | ReturnType<typeof hideAlert>
  | ReturnType<typeof showLoadingOverlay>
  | ReturnType<typeof hideLoadingOverlay>
  | ReturnType<typeof showAlertAsync>
  | ReturnType<typeof hideAlertAsync>
  | ReturnType<typeof showLoadingOverlayAsync>
  | ReturnType<typeof hideLoadingOverlayAsync>
  | ReturnType<typeof setUnreadMessages>
  | ReturnType<typeof showSelectedLanguageModal>
  | ReturnType<typeof hideSelectedLanguageModal>
  | ReturnType<typeof showConfirmModal>
  | ReturnType<typeof hideConfirmModal>
  | ReturnType<typeof setLanguage>
  | ReturnType<typeof showVideoModal>
  | ReturnType<typeof hideVideoModal>
  | ReturnType<typeof setMedia>
  | ReturnType<typeof showCustomModal>
  | ReturnType<typeof hideCustomModal>
  | ReturnType<typeof setFirstScreenOpenApp>
  | ReturnType<typeof showSelectBottomModal>
  | ReturnType<typeof hideSelectBottomModal>;
